import React from 'react'
import UserData from './user_data/UserData';
import { observer } from 'mobx-react';
import LoginForm from './LoginForm';
import SubmitButton from './SubmitButton';
import AreaChart from './Charts/AreaChart';
import './App.css';

class App extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            data: {}
        };
    }

    async componentDidMount() {
        try {
            let get_result = await fetch('/isLoggedIn', {
                method: 'post',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                }
            });

            let result = await get_result.json();

            if(result && result.success) {
                UserData.isLoggedIn = true;
                UserData.username = result.username;
                let url = "https://api.github.com/repositories/19438/issues";
                let fetch_json = await fetch(url);
                const data = await fetch_json.json();
                const chartData = [];
                for(var i = 0; i < data.length; i++)
                {
                    chartData.push({
                        label: data[i].comments,
                        value: data[i].comments,
                        tooltipContent: `<b>x: </b>${data[i].created_at}<br><b>y: </b>${data[i].comments}`
                    });
                }
                this.setState({
                    data: chartData
                });
                UserData.loading = false;
            }
            else {
                UserData.loading = false;
                UserData.isLoggedIn = false;
            }
        }

        catch(e) {
            UserData.loading = false;
            UserData.isLoggedIn = false;
        }
    }

    async doLogout() {
        try {
            let get_result = await fetch('/logout', {
                method: 'post',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                }
            });

            let result = await get_result.json();

            if(result && result.success) {
                UserData.isLoggedIn = false;
                UserData.username = '';
            }
        }

        catch(e) {
            console.log(e);
        }
    }

    render() {

        if(UserData.loading) {
            return (
                <div className="app">
                    <div className="container">
                        Loading, please wait...
                    </div>
                </div>
            );
        }

        else {
            if(UserData.isLoggedIn) {
                return (
                    <div className="app">
                        <div className="container">
                            <AreaChart
                                svgProps={{
                                    margin: { top: 80, bottom: 80, left: 80, right: 80 },
                                    width: 600,
                                    height: 400,
                                }}
                                axisProps={{
                                    xLabel: 'X Axis',
                                    yLabel: 'Y Axis',
                                }}
                                data={this.state.data}
                                strokeWidth={4}
                            />
                            <SubmitButton
                                text={ 'Logout' }
                                disabled = { false }
                                onClick = { () => this.doLogout() }
                            />
                        </div>
                    </div>
                );
            }
            return (
                <div className="app">
                    <div className="container">
                        <LoginForm/>
                    </div>
                </div>
            );
        }
    }
}

export default observer(App);
